import * as CopyToClipboard from "react-copy-to-clipboard";
import * as QRCode from "qrcode.react";
import * as React from "react";

import CopiedIcon from "./images/copied.svg";
import CopyIcon from "./images/copy_icon.svg";
import { Util } from "./util";
import styled, { keyframes } from "styled-components";
import { useTheme } from "./theme";
import { useLayoutEffect, useRef, useState } from "react";

const OuterDiv = styled.div`
    font-family: 'Source Code Pro', monospace;
    background-color: ${(props) => props.theme.outputBackground};
    padding: 0.1em;
    font-size: 1.75em;
    text-align: center;
    float: center;
    margin: 0 auto;
`;

const OuterDivInner = styled.div`
    display: flex;
    width: 100%;
    padding: 10px;
    box-sizing: border-box;
`;

const PasswordFakeInput = styled.input`
    color: ${(props) => props.theme.text};
    font-family: 'Source Code Pro', monospace;
    background-color: ${(props) => props.theme.outputBackground};
    width: 100%;
    text-align: center;
    float: center;
    border-style: dotted;
    border-radius: 0.3em;
    border-color: #d1c666;
    border-width: 0.1em;
    z-index: 2;
`;

interface ExpirationProps {
    timeToExpiration: string;
}

const AutoFillAnim = keyframes`
    to {
        background-size: 100% 15%, 100% 15%;
    }
`;

const PasswordFakeInputWithExpiration = styled(PasswordFakeInput) <ExpirationProps>`
    background-image:
        linear-gradient(${(props) => props.theme.errorText}, ${(props) => props.theme.errorText}),
        linear-gradient(${(props) => props.theme.outputBackground}, ${(props) => props.theme.outputBackground});
    background-position: 0px 104%, 0px 104%;
    background-size: 0% 15%, 100% 15%;
    background-repeat: no-repeat;


    animation: ${AutoFillAnim} ${(p) => p.timeToExpiration} linear 1;
    animation-fill-mode: forwards;
`;

const CopyButton = styled.button`
    margin-left: 0.4em;
    width: 55px;
    height: 50px;
    border-radius: 5px;
    background-color: ${(props) => props.theme.copyButton};
    border-style: outset;

    &:disabled {
        opacity: 0.5;
    }

    &:active:not([disabled])  {
        background-color: ${(props) => props.theme.copyButtonPressed};
        border-style: inset;
    }
`;

interface IconProps {
    src: string;
}

const Icon = styled.div<IconProps>`
    background: ${(props) => props.theme.text};
    width: 35px;
    height: 35px;
    mask: url(${(props) => props.src}) no-repeat;
    mask-size: contain;
`;

const QRCodeContainer = styled.div`
    display: block;
`;

declare class QRCodeWrapperProps {
    public password?: string;
}

function QRCodeWrapper({ password }: QRCodeWrapperProps): JSX.Element {
    if (password) {
        return <QRCodeContainer><QRCode value={password} /></QRCodeContainer>;
    }

    const theme = useTheme();

    return <QRCodeContainer>
        <QRCode value={"invalid"} bgColor="white" fgColor={theme.disabledQRCode} />
    </QRCodeContainer >;
}

interface PasswordBoxProps {
    text: string;
    expirationTime?: number;
}

function PasswordBox(props: PasswordBoxProps): JSX.Element {
    const passwordFieldRef = useRef<HTMLInputElement>(null);

    let timeToExpiration = undefined;

    if (props.expirationTime) {
        const now = new Date().getTime();
        const diff = props.expirationTime - now;

        timeToExpiration = `${diff}ms`;
    }

    const refreshPassSize = () => {
        const div = passwordFieldRef.current;
        if (div) {
            const w = div.clientWidth;
            const len = div.value.length;
            // this more or less converts whatever unit is in clientWidth to em, lol
            const fudge = 0.05;
            const maxSize = 1.1;
            const size = Math.min(maxSize, w / len * fudge);

            div.style.fontSize = size + "em";
        }
    };

    useLayoutEffect(() => {
        window.addEventListener("resize", refreshPassSize);
        refreshPassSize();

        return () => window.removeEventListener("resize", refreshPassSize);
    });

    if (timeToExpiration) {
        // make sure to force redraw based on the text, to reset the bar
        return <PasswordFakeInputWithExpiration
            key={props.text}
            ref={passwordFieldRef}
            timeToExpiration={timeToExpiration}
            readOnly value={props.text}
        />;
    }
    else {
        return <PasswordFakeInput
            ref={passwordFieldRef}
            readOnly value={props.text}
        />;
    }
}

interface UIOutDivProps {
    password?: string;
    generateTip?: string;
    expirationTime: number;
}

export function UIOutDiv({ password, generateTip, expirationTime }: UIOutDivProps): JSX.Element {

    const [copied, setCopied] = useState(0);
    const buttonRef = useRef<HTMLButtonElement>(null);
    const countRef = useRef<number>(0);

    countRef.current = copied;

    const onCopy = async () => {
        setCopied(copied + 1);

        // wait one second
        await Util.asyncTimeout(1000);

        setCopied(countRef.current - 1);
    };

    const onKeyDown = (event: KeyboardEvent) => {
        if ((event.ctrlKey || event.metaKey) && event.key == "c") {
            buttonRef.current?.click();
        }
    };

    React.useEffect(() => {
        document.body.addEventListener('keydown', onKeyDown);

        return () => document.body.removeEventListener('keydown', onKeyDown);
    })

    let inner;
    if (generateTip) {
        inner = <>
            <PasswordBox text={generateTip} />
            <CopyButton disabled={true} >
                <Icon src={CopyIcon} />
            </CopyButton>
        </>;
    }
    else if (password) {
        inner = <>
            <PasswordBox text={password} expirationTime={expirationTime} />
            <CopyToClipboard
                text={password}
                onCopy={onCopy}>
                <CopyButton ref={buttonRef}>
                    <Icon src={(copied > 0) ? CopiedIcon : CopyIcon} />
                </CopyButton>
            </CopyToClipboard>
        </>;
    }

    return <OuterDiv>
        <OuterDivInner>
            {inner}
        </OuterDivInner>
        <QRCodeWrapper password={password} />
    </OuterDiv>;
}
